<template>
    <section id="resources-search" class="grid-container grid-container-padded align-center">

        <!-- loop over all letters, set the ones active which actually contain entries -->
        <ul class="pagination center">
            <li v-for="letter in alphabet" :key="letter.id" :class="{ 'active' : letter.count > 0 }">
                <a @click="filterByLetters(letter.title)" :aria-label="'Search' + letter.title">{{ letter.title }}</a>
            </li>
        </ul>

        <form class="search-form" @submit.prevent="searchResources()">
            <input type="search" id="searchResource" v-model="searchResource" placeholder="Search">
            <button type="submit" id="searchSubmit"><img src="/images/search.svg" alt="search"></button>
        </form>

        <button class="filter-toggle-resources"><img src="/images/filter-toggle.png" alt="Filter Toggle"></button>
        <div class="grid-x grid-margin-x grid-padding-x">
             <div class="large-4 cell filters-area">
                <div class="mobile-filter text-center">
                    <span class="mobile-filter-title">Filter</span>
                </div>

                <span class="filter-heading">Types</span>

                <div class="checkboxes">
                    <div class="styled-checkbox" v-for="type in setTypes" :key="'type-' + type.id">
                        <input type="checkbox" class="filter-checkbox" :id="'type-' + type.id" :value="type.title">
                        <label @click="filterByTypes(type.id)" :for="'type-' + type.id">{{ type.title }}</label>
                    </div>
                </div>

                <span class="filter-heading">Categories</span>

                <div class="checkboxes">
                    <div class="styled-checkbox" v-for="category in setCategories" :key="'category-' + category.id">
                        <input type="checkbox" class="filter-checkbox" :id="'category-' + category.id" :value="category.title">
                        <label @click="filterByCategories(category.id)" :for="'category-' + category.id">{{ category.title }}</label>
                    </div>
                </div>

                <div class="mobile-filter button-area text-center">
                    <button class="btn-lg btn-clear" @click="clearFilters()"><span>Clear</span></button>
                    <button class="btn-lg btn-apply">Apply</button>
                </div>
            </div>
            <div class="filter-pills-container hide">
                <div class="filter-pills">
                    <span class="filter-pill-title">Types</span>
                    <div class="type-pills">
                        <button href="#" class="filter-pill">General Information</button>
                        <button href="#" class="filter-pill">Forms and Information</button>
                    </div>
                    <span class="filter-pill-title">Categories</span>
                    <div class="cat-pills">
                        <button href="#" class="filter-pill">Rider</button>
                        <button href="#" class="filter-pill">Official</button>
                    </div>
                </div>
            </div>

            <div class="large-8 cell resources-output">
                <div class="grid-x grid-margin-x grid-padding-x">

                    <template v-if="setResources.length === 0">

                        <div class="large-12 cell">
                        <span class="nothing-found">No resources found using your search criteria</span>
                        </div>

                    </template>

                    <template v-else>

                        <div class="large-6 cell">

                            <div v-for="letter in setResources.slice(0, Math.ceil(totalResources / 2))" :key="'letter-id-' + letter.id">
                                <div class="resource-letter">{{ letter.title }}</div>
                                <div class="resource-links">
                                    <a v-for="resource in letter.resources" :key="'resource-id-' + resource.id" :href="resource.asset" _target="blank">
                                        {{ resource.title }}
                                    </a>
                                </div>
                            </div>

                        </div>

                        <div class="large-6 cell">

                            <div v-for="letter in setResources.slice(Math.ceil(totalResources / 2), totalResources)" :key="'letter-id-' + letter.id">
                                <div class="resource-letter">{{ letter.title }}</div>
                                <div class="resource-links">
                                    <a v-for="resource in letter.resources" :key="'resource-id-' + resource.id" :href="resource.asset" _target="blank">
                                        {{ resource.title }}
                                    </a>
                                </div>
                            </div>

                        </div>

                    </template>

                </div>
                <hr class="spacer">
                <span class="resource-heading">Frequently Searched</span>

                <a href="#" class="frequent-link">Lorem Ipsum</a>
                <a href="#" class="frequent-link">Lorem Ipsum</a>
                <a href="#" class="frequent-link">Lorem Ipsum</a>
                <a href="#" class="frequent-link">Lorem Ipsum</a>

                <span class="resource-heading">Frequently Asked Questions</span>
                <ul class="accordion" data-accordion data-multi-expand="false" data-allow-all-closed="true">
                    <li class="accordion-item" data-accordion-item>
                        <a href="#" class="accordion-title">Lorem ipsum dolor sit amet?</a>
                        <div class="accordion-content" data-tab-content >
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris id finibus ex. Proin sit amet risus ut massa elementum lobortis eu vitae ligula. In eget dolor nibh. Nulla quis odio augue. Cras a pretium risus, id efficitur eros. Nulla facilisi. Nam in sapien.</p>
                        </div>
                    </li>
                    <li class="accordion-item" data-accordion-item>
                        <a href="#" class="accordion-title">Fusce luctus lorem velit, a fringilla elit?</a>
                        <div class="accordion-content" data-tab-content>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris id finibus ex. Proin sit amet risus ut massa elementum lobortis eu vitae ligula. In eget dolor nibh. Nulla quis odio augue. Cras a pretium risus, id efficitur eros. Nulla facilisi. Nam in sapien.</p>
                        </div>
                    </li>
                    <li class="accordion-item" data-accordion-item>
                        <a href="#" class="accordion-title">Integer neque velit, tempor quis magna et?</a>
                        <div class="accordion-content" data-tab-content>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris id finibus ex. Proin sit amet risus ut massa elementum lobortis eu vitae ligula. In eget dolor nibh. Nulla quis odio augue. Cras a pretium risus, id efficitur eros. Nulla facilisi. Nam in sapien.</p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>

    </section>
</template>

<script>

import {RESOURCES} from './resources-api';

export default {
    props: {

    },
    data: () => ({
        alphabet: [],
        types: [],
        categories: [],
        resources: [],
        filters: {
            title: '',
            category: [],
            type: [],
        },
        searchResource: '',
    }),
    computed: {

        setTypes: function () {
            return this.types.filter(type => type.total > 0)
        },

        setCategories: function () {
            return this.categories.filter(category => category.total > 0)
        },

        setResources: function () {

            var results = this.resources.filter(resource => resource.count > 0);

            Array.prototype.filterBy = function(filters) {
                var resourceArray = [];
                var title = typeof filters !== 'undefined' && 'title' in filters? filters.title : null;
                var category = typeof filters !== 'undefined' && 'category' in filters? filters.category : [];
                var type = typeof filters !== 'undefined' && 'type' in filters? filters.type : [];

                var max = this.length;
                for(var i = 0; i < max; i++){
                    var letter = null;
                    // check if title was filtered
                    if(title.length !== 0){
                        if(this[i].title === title){
                            letter = this[i];
                        }
                    }else{
                        letter = this[i];
                    }

                    //check if filter by category
                    var resources = [];
                    if((category.length !== 0 || type.length !== 0) && letter !== null){
                        var l = this[i].resources.length;
                        for(var x = 0; x < l; x++){

                            if('categories' in this[i].resources[x] &&
                                'types' in this[i].resources[x] &&
                                typeof this[i].resources[x].categories !== 'undefined' &&
                                typeof this[i].resources[x].types !== 'undefined' &&
                                (category.length === 0 || category.indexOf(parseInt(this[i].resources[x].categories.id)) !== -1) &&
                                (type.length === 0 || type.indexOf(parseInt(this[i].resources[x].types.id)) !== -1)){
                                resources.push(this[i].resources[x]);
                            }
                        }
                    } else {
                        resources = this[i].resources;
                    }

                    if(letter !== null && resources.length !== 0) {
                        letter.resources = resources;
                        resourceArray.push(letter);
                    }
                }
                return resourceArray;
            }

            return results.filterBy(this.filters);

        },

        totalResources: function () {
            return this.setResources.length;
        }

    },
    methods: {
        clearFilters() {
            this.filters.title = '';
            this.filters.category = [];
            this.filters.type = [];
        },

        fetchNav() {
            RESOURCES.get('by-letter.json')
                .then((response) => {
                    this.alphabet = response.data.data;
                    this.resources = response.data.data;
                })
        },

        fetchTypes() {
            RESOURCES.get('types.json')
            .then((response) => {
                this.types = response.data.data;
            })
        },

        fetchCategories() {
            RESOURCES.get('categories.json')
            .then((response) => {
                this.categories = response.data.data;
            })
        },

        filterByLetters(letter) {

            this.filters.title === letter ? this.filters.title = '' : this.filters.title = letter;

        },

        filterByTypes(type) {
            this.filters.type.indexOf(parseInt(type)) !== -1 ? this.filters.type.pop(parseInt(type)) : this.filters.type.push(parseInt(type));
        },

        filterByCategories(category) {
            this.filters.category.indexOf(parseInt(category)) !== -1 ? this.filters.category.pop(parseInt(category)) : this.filters.category.push(parseInt(category));
        },

        searchResources() {
            RESOURCES.get('search.json?q=' + this.searchResource)
            .then((response) => {
                this.resources = response.data.data;
            })
        }
    },
    created() {
        this.fetchNav();
        this.fetchTypes();
        this.fetchCategories();
    }
}

</script>


